@import "../../global.scss";

.topbar{
    width: 100%;
    height: 70px;
    background-color:  rgb(250, 250, 250);
    color:$mainColor;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease ;
    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .left{
            display: flex;
            align-items: center;
            .logo{
                font-family: 'Courier New', Courier, monospace;
                font-size: 40px;
                 font-weight: 700;
                text-decoration: none;   
                color:inherit; 
               margin-right: 40px;
               transition: all 1s ease;
            }
            .itemC{
                display: flex;
                align-items: center;
                margin-left: 30px;
                h4{
                    font-size: 15px;
                    font-family: 'Courier New', Courier, monospace;
                }
                @include mobile{
                    display: none;
                }
                .icon{
                    font-size: 18px;
                    margin-right: 5px;

                }
                span{
                    font-size: 15px;
                    font-weight: 400;
                }

                
            }


        }

        .right{
            .hamburger{
                width: 38px;
                height: 30px;
                display: flex;
                flex-direction: column;
                justify-content:space-between ;
                cursor: pointer;
               

                span{
                    width: 100%;
                    height: 3px;
                    background-color: $mainColor;
                    transform-origin: left;
                    transition: all 2s ease;
                    
                }
            }
        }
    }
    &.active{
        background-color: $mainColor;
        color: white;
        .logo{
            color:white;
            transition: all 1s ease;
       
           }

        .hamburger{
            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;

                }
                &:last-child{
                    background-color: white;
                    transform: rotate(-45deg);
                }
            }
        }

    }
}